/**
 *
 * CSS Style Object
 *
 * @typedef {Object} CssObject
 *
 * @property {String} alignContent - Sets or returns the alignment between the lines inside a flexible container when the items do not use all available space
 * @property {String} alignItems - Sets or returns the alignment for items inside a flexible container
 * @property {String} alignSelf - Sets or returns the alignment for selected items inside a flexible container
 * @property {String} animation - A shorthand property for all the animation properties below, except the animationPlayState property
 * @property {String} animationDelay - Sets or returns when the animation will start
 * @property {String} animationDirection - Sets or returns whether or not the animation should play in reverse on alternate cycles
 * @property {String} animationDuration - Sets or returns how many seconds or milliseconds an animation takes to complete one cycle
 * @property {String} animationFillMode - 	Sets or returns what values are applied by the animation outside the time it is executing
 * @property {String} animationIterationCount - Sets or returns the number of times an animation should be played
 * @property {String} animationName - 	Sets or returns a name for the keyframes animation
 * @property {String} animationTimingFunction - 	Sets or returns the speed curve of the animation
 * @property {String} animationPlayState - 	Sets or returns whether the animation is running or paused
 * @property {String} background - 	Sets or returns all the background properties in one declaration
 * @property {String} backgroundAttachment - 	Sets or returns whether a background-image is fixed or scrolls with the page
 * @property {String} backgroundColor - Sets or returns the background-color of an element
 * @property {String} backgroundImage - Sets or returns the background-image for an element
 * @property {String} backgroundPosition - Sets or returns the starting position of a background-image
 * @property {String} backgroundRepeat - Sets or returns how to repeat (tile) a background-image
 * @property {String} backgroundClip - Sets or returns the painting area of the background
 * @property {String} backgroundOrigin - Sets or returns the positioning area of the background images
 * @property {String} backgroundSize - Sets or returns the size of the background image
 * @property {String} backfaceVisibility - Sets or returns whether or not an element should be visible when not facing the screen
 * @property {String} border - 	Sets or returns borderWidth, borderStyle, and borderColor in one declaration
 * @property {String} borderBottom - Sets or returns all the borderBottom properties in one declaration
 * @property {String} borderBottomColor - Sets or returns the color of the bottom border
 * @property {String} borderBottomLeftRadius - Sets or returns the shape of the border of the bottom-left corner
 * @property {String} borderBottomRightRadius - 	Sets or returns the shape of the border of the bottom-right corner
 * @property {String} borderBottomStyle - 	Sets or returns the style of the bottom border
 * @property {String} borderBottomWidth - 	Sets or returns the width of the bottom border
 * @property {String} borderCollapse - 	Sets or returns whether the table border should be collapsed into a single border, or not
 * @property {String} borderColor - Sets or returns the color of an element's border (can have up to four values)
 * @property {String} borderImage - A shorthand property for setting or returning all the borderImage properties
 * @property {String} borderImageOutset - Sets or returns the amount by which the border image area extends beyond the border box
 * @property {String} borderImageRepeat - Sets or returns whether the image-border should be repeated, rounded or stretched
 * @property {String} borderImageSlice - Sets or returns the inward offsets of the image-border
 * @property {String} borderImageSource - Sets or returns the image to be used as a border
 * @property {String} borderImageWidth - Sets or returns the widths of the image-border
 * @property {String} borderLeft - Sets or returns all the borderLeft properties in one declaration
 * @property {String} borderLeftColor - Sets or returns the color of the left border
 * @property {String} borderLeftStyle - Sets or returns the style of the left border
 * @property {String} borderLeftWidth - Sets or returns the width of the left border
 * @property {String} borderRadius - A shorthand property for setting or returning all the four borderRadius properties
 * @property {String} borderRight - Sets or returns all the borderRight properties in one declaration
 * @property {String} borderRightColor - Sets or returns the color of the right border
 * @property {String} borderRightStyle - Sets or returns the style of the right border
 * @property {String} borderRightWidth - Sets or returns the width of the right border
 * @property {String} borderSpacing - Sets or returns the space between cells in a table
 * @property {String} borderStyle - Sets or returns the style of an element's border (can have up to four values)
 * @property {String} borderTop - Sets or returns all the borderTop properties in one declaration
 * @property {String} borderTopColor - Sets or returns the color of the top border
 * @property {String} borderTopLeftRadius - Sets or returns the shape of the border of the top-left corne
 * @property {String} borderTopRightRadius - Sets or returns the shape of the border of the top-right corner
 * @property {String} borderTopStyle - Sets or returns the style of the top border
 * @property {String} borderTopWidth - Sets or returns the width of the top border
 * @property {String} borderWidth - Sets or returns the width of an element's border (can have up to four values)
 * @property {String} bottom - Sets or returns the bottom position of a positioned element
 * @property {String} boxDecorationBreak - Sets or returns the behaviour of the background and border of an element at page-break, or, for in-line elements, at line-break.
 * @property {String} boxShadow - Attaches one or more drop-shadows to the box
 * @property {String} boxSizing - Allows you to define certain elements to fit an area in a certain way
 * @property {String} captionSide - Sets or returns the position of the table caption
 * @property {String} clear - Sets or returns the position of the element relative to floating objects
 * @property {String} clip - Sets or returns which part of a positioned element is visible
 * @property {String} color - Sets or returns the color of the text
 * @property {String} columnCount - Sets or returns the number of columns an element should be divided into
 * @property {String} columnFill - Sets or returns how to fill columns
 * @property {String} columnGap - Sets or returns the gap between the columns
 * @property {String} columnRule - A shorthand property for setting or returning all the columnRule properties
 * @property {String} columnRuleColor - Sets or returns the color of the rule between columns
 * @property {String} columnRuleStyle - Sets or returns the style of the rule between columns
 * @property {String} columnRuleWidth - Sets or returns the width of the rule between columns
 * @property {String} columns - A shorthand property for setting or returning columnWidth and columnCount
 * @property {String} columnSpan - Sets or returns how many columns an element should span across
 * @property {String} columnWidth - Sets or returns the width of the columns
 * @property {String} content - Used with the :before and :after pseudo-elements, to insert generated content
 * @property {String} counterIncrement - Increments one or more counters
 * @property {String} counterReset - Creates or resets one or more counters
 * @property {String} cursor - Sets or returns the type of cursor to display for the mouse pointer
 * @property {String} direction - Sets or returns the text direction
 * @property {String} display - Sets or returns an element's display type
 * @property {String} emptyCells - Sets or returns whether to show the border and background of empty cells, or not
 * @property {String} filter - Sets or returns image filters (visual effects, like blur and saturation)
 * @property {String} flex - Sets or returns the length of the item, relative to the rest
 * @property {String} flexBasis - Sets or returns the initial length of a flexible item
 * @property {String} flexDirection - Sets or returns the direction of the flexible items
 * @property {String} flexFlow - A shorthand property for the flexDirection and the flexWrap properties
 * @property {String} flexGrow - Sets or returns how much the item will grow relative to the rest
 * @property {String} flexShrink - Sets or returns how the item will shrink relative to the rest
 * @property {String} flexWrap - Sets or returns whether the flexible items should wrap or not
 * @property {String} cssFloat - Sets or returns the horizontal alignment of an element
 * @property {String} font - Sets or returns fontStyle, fontVariant, fontWeight, fontSize, lineHeight, and fontFamily in one declaration
 * @property {String} fontFamily - Sets or returns the font family for text
 * @property {String} fontSize - Sets or returns the font size of the text
 * @property {String} fontStyle - Sets or returns whether the style of the font is normal, italic or oblique
 * @property {String} fontVariant - Sets or returns whether the font should be displayed in small capital letters
 * @property {String} fontWeight - Sets or returns the boldness of the font
 * @property {String} fontSizeAdjust - Preserves the readability of text when font fallback occurs
 * @property {String} fontStretch - Selects a normal, condensed, or expanded face from a font family
 * @property {String} hangingPunctuation - Specifies whether a punctuation character may be placed outside the line box
 * @property {String} height - Sets or returns the height of an element
 * @property {String} hyphens - Sets how to split words to improve the layout of paragraphs
 * @property {String} icon - Provides the author the ability to style an element with an iconic equivalent
 * @property {String} imageOrientation - Specifies a rotation in the right or clockwise direction that a user agent applies to an image
 * @property {String} isolation - Defines whether an element must create a new stacking content.
 * @property {String} justifyContent - Sets or returns the alignment between the items inside a flexible container when the items do not use all available space.
 * @property {String} left - Sets or returns the left position of a positioned element
 * @property {String} letterSpacing - Sets or returns the space between characters in a text
 * @property {String} lineHeight - Sets or returns the distance between lines in a text
 * @property {String} listStyle - Sets or returns listStyleImage, listStylePosition, and listStyleType in one declaration
 * @property {String} listStyleImage - Sets or returns an image as the list-item marker
 * @property {String} listStylePosition - Sets or returns the position of the list-item marker
 * @property {String} listStyleType - Sets or returns the list-item marker type
 * @property {String} margin - Sets or returns the margins of an element (can have up to four values)
 * @property {String} marginBottom - Sets or returns the bottom margin of an element
 * @property {String} marginLeft - Sets or returns the left margin of an element
 * @property {String} marginRight - Sets or returns the right margin of an element
 * @property {String} marginTop - Sets or returns the top margin of an element
 * @property {String} maxHeight - Sets or returns the maximum height of an element
 * @property {String} maxWidth - Sets or returns the maximum width of an element
 * @property {String} minHeight - Sets or returns the minimum height of an element
 * @property {String} minWidth - Sets or returns the minimum width of an element
 * @property {String} navDown - Sets or returns where to navigate when using the arrow-down navigation key
 * @property {String} navIndex - Sets or returns the tabbing order for an element
 * @property {String} navLeft - Sets or returns where to navigate when using the arrow-left navigation key
 * @property {String} navRight - Sets or returns where to navigate when using the arrow-right navigation key
 * @property {String} navUp - Sets or returns where to navigate when using the arrow-up navigation key
 * @property {String} objectFit - Specifies how the contents of a replaced element should be fitted to the box established by its used height and width
 * @property {String} objectPosition - Specifies the alignment of the replaced element inside its box
 * @property {String} opacity - Sets or returns the opacity level for an element
 * @property {String} order - Sets or returns the order of the flexible item, relative to the rest
 * @property {String} orphans - Sets or returns the minimum number of lines for an element that must be left at the bottom of a page when a page break occurs inside an element
 * @property {String} outline - Sets or returns all the outline properties in one declaration
 * @property {String} outlineColor - Sets or returns the color of the outline around a element
 * @property {String} outlineOffset - Offsets an outline, and draws it beyond the border edge
 * @property {String} outlineStyle - Sets or returns the style of the outline around an element
 * @property {String} outlineWidth - Sets or returns the width of the outline around an element
 * @property {String} overflow - Sets or returns what to do with content that renders outside the element box
 * @property {String} overflowX - Specifies what to do with the left/right edges of the content, if it overflows the element's content area
 * @property {String} overflowY - 	Specifies what to do with the top/bottom edges of the content, if it overflows the element's content area
 * @property {String} padding - Sets or returns the padding of an element (can have up to four values)
 * @property {String} paddingBottom - Sets or returns the bottom padding of an element
 * @property {String} paddingLeft - Sets or returns the left padding of an element
 * @property {String} paddingRight - Sets or returns the right padding of an element
 * @property {String} paddingTop - Sets or returns the top padding of an element
 * @property {String} pageBreakAfter - Sets or returns the page-break behavior after an element
 * @property {String} pageBreakBefore - Sets or returns the page-break behavior before an element
 * @property {String} pageBreakInside - Sets or returns the page-break behavior inside an element
 * @property {String} perspective - Sets or returns the perspective on how 3D elements are viewed
 * @property {String} perspectiveOrigin - Sets or returns the bottom position of 3D elements
 * @property {String} position - 	Sets or returns the type of positioning method used for an element (static, relative, absolute or fixed)
 * @property {String} quotes - Sets or returns the type of quotation marks for embedded quotations
 * @property {String} resize - Sets or returns whether or not an element is resizable by the user
 * @property {String} right - Sets or returns the right position of a positioned element
 * @property {String} tableLayout - Sets or returns the way to lay out table cells, rows, and columns
 * @property {String} tabSize - 	Sets or returns the length of the tab-character
 * @property {String} textAlign - 	Sets or returns the horizontal alignment of text
 * @property {String} textAlignLast - 	Sets or returns how the last line of a block or a line right before a forced line break is aligned when text-align is "justify"
 * @property {String} textDecoration - Sets or returns the decoration of a text
 * @property {String} textDecorationColor - Sets or returns the color of the text-decoration
 * @property {String} textDecorationLine - Sets or returns the type of line in a text-decoration
 * @property {String} textDecorationStyle - Sets or returns the style of the line in a text decoration
 * @property {String} textIndent - Sets or returns the indentation of the first line of text
 * @property {String} textJustify - Sets or returns the justification method used when text-align is "justify"
 * @property {String} textOverflow - Sets or returns what should happen when text overflows the containing element
 * @property {String} textShadow - Sets or returns the shadow effect of a text
 * @property {String} textTransform - Sets or returns the capitalization of a text
 * @property {String} top - Sets or returns the top position of a positioned element
 * @property {String} transform - Applies a 2D or 3D transformation to an element
 * @property {String} transformOrigin - Sets or returns the position of transformed elements
 * @property {String} transformStyle - Sets or returns how nested elements are rendered in 3D space
 * @property {String} transition - A shorthand property for setting or returning the four transition properties
 * @property {String} transitionProperty - Sets or returns the CSS property that the transition effect is for
 * @property {String} transitionDuration - 	Sets or returns how many seconds or milliseconds a transition effect takes to complete
 * @property {String} transitionTimingFunction - Sets or returns the speed curve of the transition effect
 * @property {String} transitionDelay - Sets or returns when the transition effect will start
 * @property {String} unicodeBidi - Sets or returns whether the text should be overridden to support multiple languages in the same document
 * @property {String} userSelect - Sets or returns whether the text of an element can be selected or not
 * @property {String} verticalAlign - Sets or returns the vertical alignment of the content in an element
 * @property {String} visibility - Sets or returns whether an element should be visible
 * @property {String} whiteSpace - Sets or returns how to handle tabs, line breaks and whitespace in a text
 * @property {String} width - Sets or returns the width of an element
 * @property {String} wordBreak - Sets or returns line breaking rules for non-CJK scripts
 * @property {String} wordSpacing - Sets or returns the spacing between words in a text
 * @property {String} wordWrap - Allows long, unbreakable words to be broken and wrap to the next line
 * @property {String} widows - Sets or returns the minimum number of lines for an element that must be visible at the top of a page
 * @property {String} zIndex - Sets or returns the stack order of a positioned element
 *
 *
 */

class domElement {
  constructor(selector) {
    this.element = typeof selector === "string" ? document.querySelectorAll(selector) : [selector];
  }
}
/**
 * Set the innerHTML of a dom element
 *
 * **Syntax**: `S(Selector).text(String)`
 *
 * @param {String} value
 *
 */
domElement.prototype.text = function (value) {
  for (let i = 0; i < this.element.length; i++) {
    this.element[i]["innerHTML"] = value;
  }
  return this;
};

// /**
//  * Set the style of a dom element
//  *
//  * **Syntax**: `S(Selector).css(property, value)`
//  *
//  * @param {String} property
//  *
//  * @param {String} value
//  *
//  * */
// domElement.prototype.css = function (property, value) {
//   for (let i = 0; i < this.element.length; i++) {
//     this.element[i]["style"][property] = value;
//   }
//   return this;
// };

/**
 * @param {CssObject} styles
 *
 * Set style of dom element
 *
 * **Syntax**: `{CSSPropertiy:`value`}`
 *
 * */
domElement.prototype.css = function (styles) {
  for (let i = 0; i < this.element.length; i++) {
    for (const key in styles) {
      this.element[i].style[key] = styles[key];
    }
  }
  return this;
};

/**
 * Remove CSS proprty
 *
 * **Syntax**: `S(Selector).removeCss(CSS proprty)`
 *
 * @param {String} property
 *
 */
domElement.prototype.removeCss = function (property) {
  for (let i = 0; i < this.element.length; i++) {
    this.element[i]["style"].removeProperty(property);
  }
  return this;
};

/**
 * Set Attribute for an HTML element
 *
 * **Syntax**: `S(Selector).attr(attribute, value)`
 *
 * @param {String} attribute attribute name
 * @param {String} value attribute value
 *
 */
domElement.prototype.attr = function (attribute, value) {
  for (let i = 0; i < this.element.length; i++) {
    this.element[i].setAttribute(attribute, value);
  }
  return this;
};

/**
 * Remove Attribute from an HTML element
 *
 * **Syntax**: `S(Selector).removeAttr(attribute)`
 *
 * @param {String} attribute attribute name
 *
 */
domElement.prototype.removeAttr = function (attribute) {
  for (let i = 0; i < this.element.length; i++) {
    this.element[i].removeAttribute(attribute);
  }
  return this;
};

/**
 * Set or Return the value attribute from an HTML element
 *
 * **Syntax**: `S(Selector).val(value)`
 *
 * @param {Object} value HTML element value
 *
 * @returns Value attribute | Void
 *
 */
domElement.prototype.val = function (value) {
  if (value || value === 0) {
    this.element[0].value = value;
  } else {
    return this.element[0].value;
  }
};

/**
 * Add event listener to an HTML element
 *
 * **Syntax**: `S(Selector).on(event, functionHandler)`
 *
 * @param {String} event event listener name
 * @param {Function} functionHandler HTML element value
 *
 */
domElement.prototype.on = function (event, functionHandler) {
  for (let i = 0; i < this.element.length; i++) {
    this.element[i]["addEventListener"](event, functionHandler);
  }
  return this;
};

/**
 * Remove event listener from an HTML element
 *
 * **Syntax**: `S(Selector).off(event, functionHandler)`
 *
 * @param {String} event event listener name
 * @param {Function} functionHandler HTML element value
 *
 */
domElement.prototype.off = function (event, functionHandler) {
  for (let i = 0; i < this.element.length; i++) {
    this.element[i]["removeEventListener"](event, functionHandler);
  }
  return this;
};

/**
 * Remove an HTML element from document
 *
 * **Syntax**: `S(Selector).remove()`
 *
 */
domElement.prototype.remove = function () {
  for (let i = 0; i < this.element.length; i++) {
    this.element[i].remove();
  }
  return this;
};

/**
 * Blur an HTML element
 *
 * **Syntax**: `S(Selector).blur()`
 *
 */
domElement.prototype.blur = function () {
  for (let i = 0; i < this.element.length; i++) {
    this.element[i].blur();
  }
  return this;
};

/**
 * Focus an HTML element
 *
 * **Syntax**: `S(Selector).focus()`
 *
 */
domElement.prototype.focus = function () {
  for (let i = 0; i < this.element.length; i++) {
    this.element[i].focus();
  }
  return this;
};

/**
 * Get the style value of an HTML element
 *
 * **Syntax**: `S(Selector).getStyle(property, toNumber)`
 *
 * @param {String} property CSS property name
 * @param {Boolean} [toNumber]
 *
 * determine the return value type (string|number)
 *
 *  **Initial Value** `False`
 *
 * @returns the value of CSS property
 *
 */
domElement.prototype.getStyle = function (property, toNumber = false) {
  if (property && !toNumber) {
    return window.getComputedStyle(this.element[0]).getPropertyValue(property);
  } else if (toNumber && property) {
    return parseFloat(window.getComputedStyle(this.element[0]).getPropertyValue(property));
  } else {
    return window.getComputedStyle(this.element[0]);
  }
};

/**
 * Get the style variable from DOM Root
 *
 * **Syntax**: `S().getCssVar(variable, toNumber)`
 *
 * @param {String} variable CSS variable name
 * @param {Boolean} [toNumber]
 *
 * determine the return value type (string|number)
 *
 *  **Initial Value** `False`
 *
 * @returns the value of the CSS variable
 *
 */
domElement.prototype.getCssVar = function (variable, toNumber = false) {
  if (!toNumber) {
    return window.getComputedStyle(document.documentElement).getPropertyValue(variable);
  } else if (toNumber) {
    return parseFloat(window.getComputedStyle(document.documentElement).getPropertyValue(variable));
  }
};

/**
 * Set a Css style variable
 *
 * **Syntax**: `S().setCssVar(variable, value)`
 *
 * @param {String} variable CSS variable name
 * @param {String} value CSS variable value
 *
 */
domElement.prototype.setCssVar = function (variable, value) {
  return document.documentElement.style.setProperty(variable, value);
};

/**
 * A wait function to be used only inside async functions
 *
 * **Syntax**: `await S().wait(time)`
 *
 * @param {Number} time time in milliseconds
 *
 */
domElement.prototype.wait = function (time) {
  return new Promise((e) => setTimeout(e, time));
};

/**
 * Animate a dom element through CSS properties
 *
 * **Syntax**: `S(Selector).animate(from, to, options)`
 *
 * @param {CssObject} [from]
 *
 * Start the animation from CSSValues
 *
 * **Syntax**: `{CSSPropertiy:`(value | +-value)`}`
 *
 * **Initial value**: `auto`
 *
 * @param {CssObject} to
 *
 * Animate to CSSValues
 *
 * **Syntax**: `{CSSPropertiy:`(value | +-value)`}`
 *
 * @param {Object} [options]
 * 
 * @param {String} [options.transition=all]
 *
 * Animation transition-property
 *
 * **Syntax**: `transition: CSSPropertiy`
 *
 * **Initial value**: `all`
 *
 * @param {String} [options.ease=ease]
 *
 * Animation transition-timing-function
 *
 * **Syntax**: `ease: ease | linear | ease-in | ease-out | ease-in-out | step-start | step-end | steps(int,start|end) | cubic-bezier(n,n,n,n) | initial | inherit`
 *
 * **Initial value**: `ease`
 *
 * @param {Boolean} [options.cleanUp=false]
 *
 * Remove CSS styles after finishing the animation
 *
 * **Syntax**: `cleanUp: true | false`
 *
 * **Initial value**: `false`
 *
 * @param {Boolean} [options.dispalyNone=false]
 *
 * Set the animated element style to display: none after cleanUp
 *
 * Works only if cleanUp === true
 *
 * **Syntax**: `dispalyNone: true | false`
 *
 * **Initial value**: `false`
 *
 * @param {Boolean} [options.repeat=false]
 *
 * Repeat the animation infinitly : true | false
 *
 * **Syntax**: `repeat: true | false`
 *
 * **Initial value**: `false`
 *
 * @param {Number} [options.delay=0]
 *
 * Delay the animation start in milli milliseconds
 *
 * **Syntax**: `delay: Number`
 *
 * **Initial value**: `0`
 *
 * @param {Number} [options.duration=300]
 *
 * Animation duration in milliseconds : Number
 *
 * **Syntax**: `duration: Number`
 *
 * **Initial value**: `300`
 *
 */
domElement.prototype.animate = async function (from, to, options) {
  from = from || {};
  to = to || {};
  options = options || {};
  options.transition = options.transition || "all";
  options.ease = options.ease || "ease";
  options.cleanUp = options.cleanUp || false;
  options.dispalyNone = options.dispalyNone || false;
  options.delay = options.delay || 0;
  options.duration = options.duration || 300;

  if (options.delay) await this.wait(options.delay);

  for (const key in from) {
    if (typeof from[key] === "number") from[key] = from[key] + "px";
    if ((from[key].includes("+") || from[key].includes("-")) && key !== "transform" && !from[key].includes("var"))
      from[key] = parseInt(window.getComputedStyle(this.element[0])[key]) + parseInt(from[key]) + "px";
    this.element[0].style[key] = from[key];
  }

  this.css({ transition: `${options.transition} 0ms` });

  await this.wait(10);

  this.css({ transition: `${options.transition} ${options.duration}ms ${options.ease}` });

  for (const key in to) {
    if (typeof to[key] === "number") to[key] = to[key] + "px";
    if ((to[key].includes("+") || to[key].includes("-")) && key !== "transform" && !to[key].includes("var"))
      to[key] = parseInt(window.getComputedStyle(this.element[0])[key]) + parseInt(to[key]) + "px";

    if (!from[key]) {
      const getStyle = window.getComputedStyle(this.element[0])[key];
      this.element[0].style[key] = getStyle;
      await this.wait(10);
    }
    this.element[0].style[key] = to[key];
  }

  if (options.cleanUp) {
    await this.wait(options.duration);
    this.removeAttr("style");
    if (options.dispalyNone) this.css({ display: "none" });
  }
  if (options.repeat) {
    await this.wait(options.cleanUp ? 20 : options.duration);
    this.animate(from, to, options);
  }
};

/**
 *
 * @param {Object} options
 *
 * @param {(String|Number)} options.x
 *
 * Scroll horizontally on the x-axis
 *
 * **Syntax**: `x: DomElement | CssSelector | Number(scrollPosition) | "max"`
 *
 * @param {(String|Number)} options.y
 *
 * Scroll vertically on the y-axis
 *
 * **Syntax**: `y: DomElement | CssSelector | Number(scrollPosition) | "max"`
 * 
 * @param {Number} options.byX
 *
 * Scroll vertically on the x-axis by a numaric value
 *
 * **Syntax**: `byX: ( - | + )Number`
 * 
 * @param {Number} options.byY
 *
 * Scroll vertically on the y-axis by a numaric value
 *
 * **Syntax**: `byY: ( - | + )Number`
 *
 * @param {String} [options.behavior="none"]
 *
 * Determine the scroll behavior
 *
 * **Syntax**: `behavior: "none" | "smooth"`
 *
 * **Initial value**: `"none"`
 *
 * @param {Number} [options.pps=10]
 *
 * Control scrolling animation speed (pixel per second) if (bahavior === "smooth")
 *
 * **Syntax**: `pps: Number`
 *
 * **Initial value**: `30`
 *
 * @param {Number} [options.offsetY=0]
 *
 * Offset vertically on the y-axis from the destination.
 *
 * **Syntax**: `offsetY: Number`
 *
 * **Initial value**: `0`
 *
 * @param {Number} [options.offsetX=0]
 *
 * Offset horizontally on the x-axis from the destination.
 *
 * **Syntax**: `offsetX: Number`
 *
 * **Initial value**: `0`
 */
domElement.prototype.scrollTo = function (options) {
  options = options || {};
  options.pps = options.pps || 30;
  options.behavior = options.behavior || "none";
  options.offsetX = options.offsetX || 0;
  options.offsetY = options.offsetY || 0;

  const container = this.element[0];
  const isRoot = container.tagName === "BODY" || container === window;
  const getMaxScroll = (axis) => {
    let maxScroll;
    if (!isRoot) {
      maxScroll =
        axis === "x"
          ? container.scrollWidth - container.clientWidth
          : container.scrollHeight - container.clientHeight;
    } else if (isRoot) {
      maxScroll = axis === "x" ? document.body.scrollWidth - window.innerWidth : document.body.scrollHeight - window.innerHeight;
    }
    return maxScroll;
  };
  const scroll = (axis) => {
    const element =
      typeof options[axis] === "string" && options[axis] !== "max"
        ? document.querySelectorAll(options[axis])[0]
        : [options[axis]][0];
    const elRect =
      typeof options[axis] === "number" || options[axis] === "max"
        ? 0
        : isRoot
        ? element.getBoundingClientRect()[axis]
        : element.getBoundingClientRect()[axis] - container.getBoundingClientRect()[axis];
    let pos =
      axis === "x"
        ? container.scrollLeft || container.scrollLeft === 0 || container.scrollX || container.scrollX === 0
        : container.scrollTop || container.scrollTop === 0 || container.scrollY || container.scrollY === 0;
    const destination =
      typeof options[axis] === "number"
        ? options[axis] + (options.offsetX || options.offsetY)
        : options[axis] === "max"
        ? getMaxScroll(axis) + (options.offsetX || options.offsetY)
        : pos + elRect + (options.offsetX || options.offsetY);
    const count = pos < destination ? "inc" : "dec";
    if (options.behavior === "smooth") {
      const scrolltl = () => {
        count === "inc" ? Math.min((pos += options.pps), destination) : Math.max((pos -= options.pps), destination);
        axis === "x" ? container.scrollTo({ left: pos }) : container.scrollTo({ top: pos });
        if ((pos < destination && count === "inc") || (pos > destination && count === "dec")) {
          window.requestAnimationFrame(scrolltl);
          console.log("running")
        }
      };
      window.requestAnimationFrame(scrolltl);
    } else axis === "x" ? container.scrollTo({ left: destination }) : container.scrollTo({ top: destination });
  };
  const scrollBy = (axis) => {
    let pos =
      axis === "x"
        ? container.scrollLeft || container.scrollLeft === 0 || container.scrollX || container.scrollX === 0
        : container.scrollTop || container.scrollTop === 0 || container.scrollY || container.scrollY === 0;
    const destination = axis === "x" ? options.byX : options.byY;
    let count = 0;
    if (options.behavior === "smooth") {
      const tl = () => {
        count = Math.min((count += options.pps), Math.abs(destination));
        axis === "x"
          ? container.scrollTo({ left: destination < 0 ? pos - count : pos + count })
          : container.scrollTo({ top: destination < 0 ? pos - count : pos + count });
        if (count < Math.abs(destination)) {
          window.requestAnimationFrame(tl);
          
        }
      };
      window.requestAnimationFrame(tl);
    } else axis === "x" ? container.scrollTo({ left: pos + destination }) : container.scrollTo({ top: pos + destination });
  };
  if (options.x || options.x === 0) {
    scroll("x");
  } else if (options.y || options.y === 0) {
    scroll("y");
  } else if (options.byX || options.byX === 0) {
    scrollBy("x");
  } else if (options.byY || options.byY === 0) {
    scrollBy("y");
  }
};

/**
 *
 * @param {(String|Element)} selector - CssSelector | DomElement
 */
export const S = (selector) => new domElement(selector);
